* {
  box-sizing: border-box;
}

body {
  color: #fff;
  height: 100%;
  background-color: #28272a;
  font-family: Montserrat, sans-serif;
  font-size: .83vw;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  display: block;
}

button {
  border: unset;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
}

a {
  color: inherit;
}

input, button, textarea, select {
  font: inherit;
}

body, h1, h2, h3, h4, p, ul, li {
  font-size: inherit;
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  position: relative;
}

.header_wrapper {
  width: 100%;
  z-index: 2;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.header_top {
  width: 100%;
  height: 50px;
  background: linear-gradient(#788b8c 0%, rgba(238, 130, 238, 0) 100%);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header_logo {
  width: 60px;
  height: 100%;
  background-color: #fee251;
  align-items: center;
  padding-left: 20px;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
}

@media (min-width: 1024px) {
  .header_logo {
    width: 23%;
    padding-left: 60px;
  }
}

.header_logo a {
  color: #363221;
  font-size: 2.34vw;
  font-weight: 700;
}

@media (min-width: 1024px) {
  .header_logo a {
    font-size: 1.25vw;
  }
}

.header_account {
  margin-right: 5%;
}

.header_menu {
  width: 60px;
  height: 40px;
  background-color: rgba(40, 39, 42, .4);
  position: absolute;
  top: 50px;
  overflow: hidden;
}

@media (min-width: 1024px) {
  .header_menu {
    width: 18%;
    height: 700px;
    background-color: rgba(40, 39, 42, .4);
    position: absolute;
    top: 70px;
    overflow: hidden;
  }
}

.account-list {
  align-items: center;
  gap: 60px;
  display: flex;
  position: relative;
}

.account-list:after {
  content: "";
  height: 16px;
  width: 2px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
}

.account-item {
  align-items: center;
  font-size: 2.34vw;
  font-weight: 600;
  display: flex;
}

.account-item:hover {
  color: #fee251;
  transform: scale(1.1);
}

@media (min-width: 1024px) {
